import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Reservations } from 'src/app/booking/services/reservations/reservations';
import { ReservationsService } from 'src/app/booking/services/reservations/reservations.service';
import { Rooms } from 'src/app/booking/services/rooms/rooms';
import { RoomsService } from 'src/app/booking/services/rooms/rooms.service';
import * as moment from 'moment';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-booking-master-modal-form',
  templateUrl: './booking-master-modal-form.component.html',
  styleUrls: ['./booking-master-modal-form.component.scss']
})
export class BookingMasterModalFormComponent implements OnInit {
  private webId: string = undefined;

  @Output() close: EventEmitter<null> = new EventEmitter();

  @Input() masterIsOpen = false;
  @Input() editableReservation: Reservations;
  @Input() groupedReservations: any;
  @Input() rooms: [];
  @Input() meetingTypes: [];

  datePickerConfig = {
    format: 'DD.MM.YYYY',
    firstDayOfWeek: 'mo'
  };

  public pickedRoom = null;
  public freeRooms = [];
  public duration = ' ';
  public dateStart = null;
  public dateEnd = null;
  public endHour = null;

  public firstStageParameters = {
    count: 0,
    date: null,
    time: '00:00',
    duration: this.duration,
    projector: false,
    videoStaff: false
  };

  objectKeys = Object.keys;
  public durations = {
    '0.5': '30 ',
    '1': '1 ',
    '1.5': '1.5 ',
    '2': '2 ',
    '2.5': '2.5 ',
    '3': '3 ',
    '3.5': '3.5 ',
    '4': '4 ',
    '4.5': '4.5 ',
    '5': '5 '
  };

  public isFirstStage = true;
  public isSecondStage = false;
  public isThirdStage = false;

  public changeDuration(newValue) {
    this.duration = newValue.target.value;
  }

  public changeResponsible(users) {
    if (this.editableReservation) {
      this.editableReservation.responsibleId = users && users.length > 0 ? users[0].Id : null;
    }
  }

  public changeInitiator(users) {
    if (this.editableReservation) {
      this.editableReservation.initiatorId = users && users.length > 0 ? users[0].Id : null;
    }
  }

  public changeParticipants(users) {
    if (this.editableReservation) {
      this.editableReservation.participants = users.map(user => {
        return {
          Id : user.Id,
          Title: user.Title,
          UserName: user.LoginName,
          EMail: user.Email
        };
      });
    }
  }

  public timeCheck() {
    const time = moment(this.firstStageParameters.time, 'HH:mm');
    this.dateStart = this.firstStageParameters.date.set({
      hour:   time.get('hour'),
      minute: time.get('minute'),
      second: time.get('second')
    });
    this.dateEnd = moment(this.dateStart).add(Number(this.duration), 'hours');
    this.endHour = this.dateEnd.format('HH:mm');
    let filter =  `((datetime'${moment(this.dateStart).toISOString()}' ge EventDate and datetime'${moment(this.dateStart).toISOString()}' lt EndDate)`;
    filter += ` or (datetime'${moment(this.dateEnd).toISOString()}' gt EventDate and datetime'${moment(this.dateEnd).toISOString()}' le EndDate))`;
    filter += ` or (datetime'${moment(this.dateStart).toISOString()}' lt EventDate and datetime'${moment(this.dateEnd).toISOString()}' gt EndDate)`;
    this.reservationsService.getItems({
      filter: filter
    }).subscribe((reservations: Reservations[]) => {
      reservations.forEach(reservation => {
        this.freeRooms.forEach(room => {
          if (reservation.roomLookupId === room.id) {
            this.freeRooms.splice(this.freeRooms.indexOf(room), 1);
          }
        });
      });
    });
  }

  public goToSecondStage() {
    const videoStaff = (this.firstStageParameters.videoStaff) === false ? '' : '';
    const projector = (this.firstStageParameters.projector === false) ? '' : '';
    this.timeCheck();
    if (((this.firstStageParameters.count === undefined) ? '' : this.firstStageParameters.count) <= 0) {
      alert(' " "   ');
    } else if (this.firstStageParameters.date.length === null) {
      alert(' ""   ');
    } else if ((moment(this.firstStageParameters.date + this.firstStageParameters.time, 'DD.MM.YYYY HH:mm').unix() < moment(this.firstStageParameters.date + '08:00', 'DD.MM.YYYY HH:mm').unix()) ||
    (moment(this.firstStageParameters.date + this.firstStageParameters.time, 'DD.MM.YYYY HH:mm').unix() > moment(this.firstStageParameters.date + '19:30', 'DD.MM.YYYY HH:mm').unix())) {
      alert(' ""   ,       08:00  19:30 ');
    } else if (this.duration === ' ') {
      alert('   ');
    } else if (parseInt(this.endHour.replace(':', ''), 10) > 2000) {
      alert('    ,   20:00');
    } else if (this.firstStageParameters.videoStaff === false && this.firstStageParameters.projector === false) {
      this.roomsService.getItems().subscribe((rooms: Rooms[]) => {
        rooms.forEach(room => {
          this.freeRooms.push(room);
        });
      });
    } else {
      this.roomsService.getItems ({
        filter: `slVideoStaff eq '${videoStaff}' and slProjector eq '${projector}'`
      }).subscribe((rooms: Rooms[] ) => {
        rooms.forEach(room => {
          this.freeRooms.push(room);
        });
      });
    }
    this.timeCheck();
    this.isFirstStage = false;
    this.isSecondStage = true;
  }

  public goToThirdStage() {
    this.editableReservation.roomLookupId = this.pickedRoom.id;
    this.editableReservation.officeLookupId = this.pickedRoom.officeLookupId;
    this.editableReservation.count = this.firstStageParameters.count;
    this.editableReservation.projector = (this.firstStageParameters.projector === true ? '' : '');
    this.editableReservation.videoStaff = (this.firstStageParameters.videoStaff === true ? '' : '');
    this.editableReservation.eventDate = moment(this.dateStart).toISOString();
    this.editableReservation.endDate = moment(this.dateEnd).toISOString();
    this.editableReservation.title = undefined;
    this.editableReservation.meetingTypeLookupId = undefined;
    this.editableReservation.description = undefined;
    this.isSecondStage = false;
    this.isThirdStage = true;
  }

  public createReservation(item: Reservations) {
    if (((item.title === undefined) ? '' : item.title).length <= 0) {
      alert(' " "   ');
    } else if (item.meetingTypeLookupId === undefined) {
      alert(' " "   ');
    } else if (item.initiatorId === undefined) {
      alert(' ""   ');
    } else if (item.responsibleId === undefined) {
      alert(' ""   ');
    } else if (item.participants === undefined) {
      alert(' ""   ');
    } else {
      this.timeCheck();
      const freeRoomIds = [];
      this.freeRooms.forEach(room => {
        freeRoomIds.push(room.id);
      });
      if (freeRoomIds.indexOf(item.roomLookupId) !== -1) {
        this.reservationsService.createItem({
          id: undefined,
          title: item.title,
          participants: {'results' : item.participants.map(user => user.Id)},
          videoStaff: item.videoStaff,
          projector: item.projector,
          description: item.description,
          initiator: item.initiator,
          initiatorId: item.initiatorId,
          initiatorLogin: item.initiatorLogin,
          responsible: item.responsible,
          responsibleId: item.responsibleId,
          responsibleLogin: item.responsibleLogin,
          count: item.count,
          eventDate: item.eventDate,
          endDate: item.endDate,
          companyLookupId: item.companyLookupId,
          officeLookupId: item.officeLookupId,
          roomLookupId: item.roomLookupId,
          meetingTypeLookupId: item.meetingTypeLookupId
        }, this.webId).subscribe((itemResived) => {
          this.resetMaster();
        });
      } else {
        alert('     ');
        this.isFirstStage = true;
        this.isSecondStage = false;
        this.isThirdStage = false;
      }
    }
  }

  public resetMaster() {
    this.isFirstStage = true;
    this.isSecondStage = false;
    this.isThirdStage = false;
    this.masterIsOpen = false;
    this.editableReservation = null;
    this.close.emit();
  }

  constructor(
    private roomsService: RoomsService,
    private route: ActivatedRoute,
    private reservationsService: ReservationsService) {}

  ngOnInit() {
    this.webId =
      this.route.snapshot.data.webId !== 'root'
        ? this.route.snapshot.data.webId
        : null;
  }
}
